/*
< ----------- ANIMATED CHECKBOX ----------- >
*/
.animate-checkbox .tg-list {
  display: flex;
  align-items: center;
  text-align: center;
}

.animate-checkbox .tg-list-item {
  margin: 0 2em;
}

.animate-checkbox .tgl {
  display: none;
}

.animate-checkbox .tgl::after, .animate-checkbox .tgl::before,
.animate-checkbox .tgl *,
.animate-checkbox .tgl *::after,
.animate-checkbox .tgl *::before,
.animate-checkbox .tgl + .tgl-btn {
  box-sizing: border-box;
}

.animate-checkbox .tgl::after::selection, .animate-checkbox .tgl::before::selection,
.animate-checkbox .tgl *::selection,
.animate-checkbox .tgl *::after::selection,
.animate-checkbox .tgl *::before::selection,
.animate-checkbox .tgl + .tgl-btn::selection {
  background: none;
}

.animate-checkbox .tgl + .tgl-btn {
  position: relative;
  display: inline-block;
  width: 7rem;
  height: 1.8rem;
  cursor: pointer;
  user-select: none;
  outline: 0;
}

.animate-checkbox .tgl + .tgl-btn::after, .animate-checkbox .tgl + .tgl-btn::before {
  position: relative;
  display: inline-block;
  width: 50%;
  height: 100%;
  content: "";
}

.animate-checkbox .tgl + .tgl-btn::after {
  left: 0;
}

.animate-checkbox .tgl + .tgl-btn::before {
  display: none;
}

.animate-checkbox .tgl:checked + .tgl-btn::after {
  left: 50%;
}

.animate-checkbox .tgl-skewed + .tgl-btn {
  overflow: hidden;
  font-family: "Roboto Light", sans-serif;
  background: #28a745;
  transition: all .2s ease;
  transform: skew(-10deg);
  backface-visibility: hidden;
}

.animate-checkbox .tgl-skewed + .tgl-btn::after, .animate-checkbox .tgl-skewed + .tgl-btn::before {
  position: absolute;
  display: inline-block;
  width: 100%;
  font-weight: 700;
  line-height: 2em;
  color: #fff;
  text-align: center;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
  transition: all .2s ease;
  transform: skew(10deg);
}

.animate-checkbox .tgl-skewed + .tgl-btn::after {
  left: 100%;
  content: attr(data-tg-on);
}

.animate-checkbox .tgl-skewed + .tgl-btn::before {
  left: 0;
  content: attr(data-tg-off);
}

.animate-checkbox .tgl-skewed + .tgl-btn:active {
  background: #28a745;
}

.animate-checkbox .tgl-skewed + .tgl-btn:active::before {
  left: -10%;
}

.animate-checkbox .tgl-skewed:checked + .tgl-btn {
  background: #888;
}

.animate-checkbox .tgl-skewed:checked + .tgl-btn::before {
  left: -100%;
}

.animate-checkbox .tgl-skewed:checked + .tgl-btn::after {
  left: 0;
}

.animate-checkbox .tgl-skewed:checked + .tgl-btn:active::after {
  left: 10%;
}

