  background: none;
  box-shadow: none;
}

.progress::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  border: 12px solid #fff;
  border-radius: 50%;
}

.progress > span {
  position: absolute;
  top: 0;
  z-index: 1;
  width: 50%;
  height: 100%;
  overflow: hidden;
}

.progress .progress-bar {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: none;
  border-style: solid;
  border-width: 12px;
}

.progress .progress-left {
  left: 0;
}

.progress .progress-left .progress-bar {
  left: 100%;
  border-left: 0;
  border-top-right-radius: 80px;
  border-bottom-right-radius: 80px;
  transform-origin: center left;
}

.progress .progress-right {
  right: 0;
}

.progress .progress-right .progress-bar {
  left: -100%;
  border-right: 0;
  border-top-left-radius: 80px;
  border-bottom-left-radius: 80px;
  transform-origin: center right;
  animation: loading-1 1.8s linear forwards;
}

.progress .progress-value {
  position: absolute;
  top: 5%;
  left: 5%;
  width: 90%;
  height: 90%;
  font-size: 24px;
  line-height: 135px;
  color: #fff;
